.card {
    position: relative;
    cursor: pointer;
}

.card:hover .card__img {
    animation: opacity 0.6s ease-in-out;
}

@keyframes opacity {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0.8;
    }
    100% {
        opacity: 1;
    }
}

.card:hover .card__icon {
    animation: slide 0.5s ease-in-out;
}

@keyframes slide {
    0% {
        left: 0;
    }
    50% {
        left: -20px;
    }
    100% {
        left: 0;
    }
}

.card__link {
    position: absolute;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
}
.card__img {
    margin-bottom: 28px;
    border-radius: 20px;
}

.card__body {
    display: flex;
    justify-content: space-between;
    column-gap: 10px;
    align-items: center;
}

.card__title {
    font-weight: 500;
    font-size: 32px;
    line-height: 1.56;
    letter-spacing: -0.04em;
}

.card__muted {
    font-weight: 500;
    font-size: 24px;
    line-height: 2.8;
    letter-spacing: -0.08em;
    color: var(--gray);
}

.card__icon {
    position: relative;
    left: 0;
}
@media (max-width: 1200px) {
    .card__title {
        font-size: 26px;
        line-height: 0.96;
    }
    .card__muted {
        font-size: 18px;
        line-height: 2.7;
    }
    .card {
        margin-bottom: 50px;
    }
}
